/* ContactList.module.css */
.list {
    list-style: none;
    width: 50%;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
            background-color: rgba(255, 255, 255, 0.8);
                border-radius: 8px;
}

.item {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #ccc;
}

.button {
    padding: 5px 10px;
    background-color: #ff4d4d;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.button:hover {
    background-color: #cc0000;
}